    // Calculate

    function calculate() {
      $("#preloaderOverlay").addClass("active");
      $("body").addClass("overflow-hidden");

      let f_name = $(".calculator-form").find("#f_name").val();
      let f_gender = $(".calculator-form")
        .find('input[name="gender"]:checked')
        .val();
      let f_age = $(".calculator-form").find("#f_age").val();
      let f_phone = $(".calculator-form").find("#f_phone").val();
      let f_residence = $(".calculator-form").find("#f_residence").val();
      let f_marital_status = $(".calculator-form")
        .find('input[name="marital_status"]:checked')
        .val();
      let f_children = $(".calculator-form").find("#f_children").val();
      let f_dependent = $(".calculator-form").find("#f_dependent").val();
      let f_life_insuranced = $(".calculator-form")
        .find('input[name="life_insuranced"]:checked')
        .val();

      let f_salary = $(".calculator-form").find("#f_salary").val();
      let AN_salary = AutoNumeric.getAutoNumericElement(
        $(".calculator-form").find("#f_salary")[0],
      );
      let salary_val = AN_salary.getNumber();

      let f_other_income = $(".calculator-form").find("#f_other_income").val();
      let AN_other_income = AutoNumeric.getAutoNumericElement(
        $(".calculator-form").find("#f_other_income")[0],
      );
      let other_income_val = AN_other_income.getNumber();

      let f_savings = $(".calculator-form").find("#f_savings").val();
      let AN_savings = AutoNumeric.getAutoNumericElement(
        $(".calculator-form").find("#f_savings")[0],
      );
      let savings_val = AN_savings.getNumber();

      let f_insurance_covers = $(".calculator-form")
        .find("#f_insurance_covers")
        .val();
      let AN_insurance_covers = AutoNumeric.getAutoNumericElement(
        $(".calculator-form").find("#f_insurance_covers")[0],
      );
      let insurance_covers_val = AN_insurance_covers.getNumber();

      let f_expenses = $(".calculator-form").find("#f_expenses").val();
      let AN_expenses = AutoNumeric.getAutoNumericElement(
        $(".calculator-form").find("#f_expenses")[0],
      );
      let expenses_val = AN_expenses.getNumber();

      let f_loans = $(".calculator-form").find("#f_loans").val();
      let AN_loans = AutoNumeric.getAutoNumericElement(
        $(".calculator-form").find("#f_loans")[0],
      );
      let loans_val = AN_loans.getNumber();

      let f_insurance_cost = $(".calculator-form")
        .find("#f_insurance_cost")
        .val();
      let AN_insurance_cost = AutoNumeric.getAutoNumericElement(
        $(".calculator-form").find("#f_insurance_cost")[0],
      );
      let insurance_cost_val = AN_insurance_cost.getNumber();

      let f_spouse_monthly_payment = $(".calculator-form")
        .find("#f_spouse_monthly_payment")
        .val();
      let AN_spouse_monthly_payment = AutoNumeric.getAutoNumericElement(
        $(".calculator-form").find("#f_spouse_monthly_payment")[0],
      );
      let spouse_monthly_payment_val = AN_spouse_monthly_payment.getNumber();

      let f_spouse_payment_years = $(".calculator-form")
        .find("#f_spouse_payment_years")
        .val();
      let f_spouse_yearly_payment = spouse_monthly_payment_val * 12;

      let f_child_monthly_payment = $(".calculator-form")
        .find("#f_child_monthly_payment")
        .val();
      let AN_child_monthly_payment = AutoNumeric.getAutoNumericElement(
        $(".calculator-form").find("#f_child_monthly_payment")[0],
      );
      let child_monthly_payment_val = AN_child_monthly_payment.getNumber();

      let f_child_payment_years = $(".calculator-form")
        .find("#f_child_payment_years")
        .val();
      let f_child_yearly_payment = child_monthly_payment_val * 12;

      let f_dependents_monthly_payment = $(".calculator-form")
        .find("#f_dependents_monthly_payment")
        .val();
      let AN_dependents_monthly_payment = AutoNumeric.getAutoNumericElement(
        $(".calculator-form").find("#f_dependents_monthly_payment")[0],
      );
      let dependents_monthly_payment_val =
        AN_dependents_monthly_payment.getNumber();

      let f_dependents_payment_years = $(".calculator-form")
        .find("#f_dependents_payment_years")
        .val();
      let f_dependents_yearly_payment = dependents_monthly_payment_val * 12;

      let result = null;
      if (
        savings_val + insurance_covers_val + other_income_val + salary_val >=
        f_spouse_yearly_payment * f_spouse_payment_years +
          f_child_yearly_payment * f_child_payment_years +
          f_dependents_yearly_payment * f_dependents_payment_years +
          expenses_val +
          loans_val
      ) {
        result = 0;
      } else {
        result = -(
          savings_val +
          insurance_covers_val +
          other_income_val +
          salary_val -
          (f_spouse_yearly_payment * f_spouse_payment_years +
            f_child_yearly_payment * f_child_payment_years +
            f_dependents_yearly_payment * f_dependents_payment_years +
            expenses_val +
            loans_val)
        );
      }

      if (isNaN(result)) {
        alert("Something went wrong");
        return;
      }

      // Fill finish screen
      $(".finish-screen__total").html(`${numberWithCommas(result)} LAK`);
      // Fill result screen
      $("#result_name").html(f_name);
      $("#result_age").html(f_age);

      // $("#result_marital").html(f_marital_status);
      if (f_marital_status === "Single") {
        $("#result_marital").find('.value[data-value="1"]').addClass("active");
      } else if (f_marital_status === "Married") {
        $("#result_marital").find('.value[data-value="2"]').addClass("active");
      } else if (f_marital_status === "Widowed") {
        $("#result_marital").find('.value[data-value="3"]').addClass("active");
      } else if (f_marital_status === "Divorced") {
        $("#result_marital").find('.value[data-value="4"]').addClass("active");
      } else if (f_marital_status === "Other") {
        $("#result_marital").find('.value[data-value="5"]').addClass("active");
      } else {
        $("#result_marital").find(".value-fallback").addClass("active");
        $("#result_marital").find(".value-fallback").html(f_marital_status);
      }

      // $("#result_residence").html(f_residence);
      if (f_residence === "Vientiane Capital") {
        $("#result_residence")
          .find('.value[data-value="1"]')
          .addClass("active");
      } else if (f_residence === "Vientiane") {
        $("#result_residence")
          .find('.value[data-value="2"]')
          .addClass("active");
      } else if (f_residence === "Savannakhet") {
        $("#result_residence")
          .find('.value[data-value="3"]')
          .addClass("active");
      } else if (f_residence === "Champasack") {
        $("#result_residence")
          .find('.value[data-value="4"]')
          .addClass("active");
      } else if (f_residence === "Luangprabang") {
        $("#result_residence")
          .find('.value[data-value="5"]')
          .addClass("active");
      } else if (f_residence === "Khammuane") {
        $("#result_residence")
          .find('.value[data-value="6"]')
          .addClass("active");
      } else if (f_residence === "Bokeo") {
        $("#result_residence")
          .find('.value[data-value="7"]')
          .addClass("active");
      } else if (f_residence === "Borikhamxay") {
        $("#result_residence")
          .find('.value[data-value="8"]')
          .addClass("active");
      } else if (f_residence === "Huaphanh") {
        $("#result_residence")
          .find('.value[data-value="9"]')
          .addClass("active");
      } else if (f_residence === "Luangnamtha") {
        $("#result_residence")
          .find('.value[data-value="10"]')
          .addClass("active");
      } else if (f_residence === "Oudomxay") {
        $("#result_residence")
          .find('.value[data-value="11"]')
          .addClass("active");
      } else if (f_residence === "Phongsaly") {
        $("#result_residence")
          .find('.value[data-value="12"]')
          .addClass("active");
      } else if (f_residence === "Saravanh") {
        $("#result_residence")
          .find('.value[data-value="13"]')
          .addClass("active");
      } else if (f_residence === "Xayabury") {
        $("#result_residence")
          .find('.value[data-value="14"]')
          .addClass("active");
      } else if (f_residence === "Xaysomboon") {
        $("#result_residence")
          .find('.value[data-value="15"]')
          .addClass("active");
      } else if (f_residence === "Xiengkhuang") {
        $("#result_residence")
          .find('.value[data-value="16"]')
          .addClass("active");
      } else if (f_residence === "Sekong") {
        $("#result_residence")
          .find('.value[data-value="17"]')
          .addClass("active");
      } else if (f_residence === "Attapeu") {
        $("#result_residence")
          .find('.value[data-value="18"]')
          .addClass("active");
      } else {
        $("#result_residence").find(".value-fallback").addClass("active");
        $("#result_residence").find(".value-fallback").html(f_residence);
      }

      $("#result_child").html(f_children);
      $("#result_dependents").html(f_dependent);
      $("#result_salary").html(f_salary);
      $("#result_savings").html(f_savings);
      $("#result_loans").html(f_loans);
      $("#result_length").html(`${length} Year(s)`);
      $("#result_total").html(`${numberWithCommas(result)} LAK`);

      // Submit
      const url = $("#calculatorForm").attr("action");
      const method = $("#calculatorForm").attr("method");
      const dataType = $("#calculatorForm").data("type") || null;
      const self = $(this);

      let requestObj = {
        name: f_name,
        gender: f_gender,
        age: f_age,
        phone: f_phone,
        residence: f_residence,
        marital_status: f_marital_status,
        children: f_children,
        dependents: f_dependent,
        life_insuranced: f_life_insuranced,
        salary: f_salary,
        other_income: f_other_income,
        savings: f_savings,
        insurance_covers: f_insurance_covers,
        expenses: f_expenses,
        loans: f_loans,
        insurance_cost: f_insurance_cost, // changed to premium cost
        spouse_monthly_payment: f_spouse_monthly_payment,
        spouse_payment_years: f_spouse_payment_years,
        child_monthly_payment: f_child_monthly_payment,
        child_payment_years: f_child_payment_years,
        dependents_monthly_payment: f_dependents_monthly_payment,
        dependents_payment_years: f_dependents_payment_years,
        total: `${numberWithCommas(result)} LAK`,
      };

      $.ajax({
        url,
        type: method,
        dataType: "json",
        data: {
          calculator_sent: true,
          ...requestObj,
        },
        success: function (data) {
          // Close preloader
          $("#preloaderOverlay").removeClass("active");
          $("body").removeClass("overflow-hidden");
          if (!data.success) {
            // Basic error handling
            alert("Something went wrong, please try again later.");
            console.error(data);
          }

          openStep4();
          stepperStep++;

          let requestId = data.request_id;
          $("#requestId").val(requestId);
        },
        error: function (data) {
          // Basic error handling
          alert("Something went wrong, please try again later.");
          console.error(data);

          $("#preloaderOverlay").removeClass("active");
          $("body").removeClass("overflow-hidden");
        },
      });
    }

