    // Field Number Control
    $(".field-number-minus").on("click touchstart", function () {
      let $parent = $(this).closest(".field-number-wrap");
      let $input = $parent.find(".field-number");
      let min = $input.attr("min");
      let max = $input.attr("max");
      let value = Number($input.val());

      if (min && value <= Number(min)) {
        return;
      }
      value = value - 1;
      $input.val(value);

      // Connect range if needed
      let $numberRangeWrap = $(this).closest(".field-number-and-range-wrap");
      if ($numberRangeWrap.length) {
        // Native range
        let $range = $numberRangeWrap.find(".field-range");
        $range.val(value);

        // Custsom plugin range
        let $customRange = $numberRangeWrap.find(".custom-range-input");
        $customRange.slider("setValue", value);
      }
      // Connect list if needed
      let $numberListWrap = $(this).closest(".field-number-and-list-wrap");
      if ($numberListWrap.length) {
        let $list = $numberListWrap.find(".icon-list");
        let icon = $list.data("icon");
        let itemsString = "";

        for (let index = 0; index < value; index++) {
          itemsString += `<li class="icon-list__item"><i class="${icon}"></i></li>`;
        }

        $list.html(itemsString);
      }
    });
